/************ NAVIGATION BAR TOP **************/
.logo-container a {
  position: relative;
  width: 175px;
  height: 80px;
}
.logo-container a img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  height: 30px;
  width: 120px;
}
.navbar-nav {
  width: 100%;
}
.navbar-nav > li {
  width: 20%;
}
#navbar-top {
  height: 80px;
  font-size: 15px;
  background-color: #376092;
  border: 0;
  margin-bottom: 0;
}
#navbar-top .navbar-brand {
  height: 80px;
  padding: 0 20px;
  font-size: 25px;
  line-height: 80px;
  text-align: center;
}
#navbar-top .navbar-nav li a {
  height: 80px;
  padding: 0 10px;
  font-size: 18px;
  line-height: 80px;
  text-align: center;
}
#navbar-top .navbar-text {
  height: 55px;
  padding: 0 10px;
  margin: 15px 5px;
  font-size: 18px;
  line-height: 55px;
  text-align: center;
}
#navbar-top li a {
  width: 100%;
}
#navbar-top li.double-line a {
  padding-top: 18px;
  line-height: 17px;
}
#navbar-top .navbar-text-small {
  font-size: .8em;
}
#navbar-top .navbar-text-large {
  font-size: 3em;
}
#navbar-top .navbar-complex {
  padding-top: -10px;
  font-size: .7em;
  line-height: 55px;
}
#navbar-top .dropdown-menu {
  background-color: #dbe5f1;
  border-radius: 0;
  border: 3px solid #376092;
  padding: 0;
  margin-top: -3px;
}
#navbar-top .dropdown-menu li a {
  border-radius: 0;
  padding: 10px 20px;
  color: #376092 !important;
  font-size: 14px;
}
#navbar-top .dropdown-menu li a:hover {
  background-color: #376092;
  color: white !important;
}
#navbar-top a {
  padding: 5px;
