div.org-node {
  display: block;
  width: 100%;
}
div.org-node a {
  color: white;
  text-decoration: none;
}
div.org-node span.org-node-info {
  display: inline-block;
  padding: 4px 10px;
  margin: 0;
  background: #dbe5f1;
}
div.org-node span.org-node-info > a {
  color: #376092;
}
div.org-node span.org-node-info i {
  color: #376092;
}
div.org-node.selected > span.org-node-info {
  background-color: #376092;
}
div.org-node.selected > span.org-node-info > a {
  color: white;
}
div.org-node.selected > span.org-node-info i {
  color: white;
}
div.org-node.hasWriteAccess span.org-node-info {
  display: inline-block;
  padding: 4px 10px;
  margin: 0;
  background: #b1cff3;
}
div.org-node.selected.hasWriteAccess > span.org-node-info {
  background: #376092;
}
div.org-node.selected.hasWriteAccess > span.org-node-info > a {
  color: white;
}
div.org-node.selected.hasWriteAccess > span.org-node-info i {
  color: white;
}
div.org-node li {
  position: relative;
  padding: 5px 5px;
  margin: 0;
  list-style-type: none;
}
div.org-node li::before,
.org-node li::after {
  position: absolute;
  right: auto;
  left: -20px;
  content: '';
}
div.org-node li::before {
  top: 5px;
  bottom: 50px;
  width: 1px;
  height: 100%;
  border-left: 2px solid #ddd;
}
div.org-node.selected li::before {
  border-color: #aaa;
}
div.org-node li:last-child::before {
  height: 15px;
}
div.org-node li::after {
  top: 20px;
  width: 20px;
  height: 15px;
  border-top: 2px solid #ddd;
}
div.org-node.selected li::after {
  border-color: #767676;
}
